.App {
  text-align: center;
}

.App-logo {
  margin-top: 50px;
}

.App-header {
  background-color: #222;
  padding: 20px;
  margin-bottom: 50px;
  color: white;
}

.App-title {
  margin-top: 5vh;
}

.App-intro {
  font-size: large;
}

.content {
  margin-top: 10vh;
}

.current-suggestion {
  padding: 10px;
  border: 3px groove goldenrod;
  border-radius: 8px;
  margin-bottom: 20px;
  -webkit-box-shadow: 5px 10px rgba(0, 0, 0, 0.1);
          box-shadow: 5px 10px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 13px 7px 43px 3px rgba(0, 0, 0, 0.6);
          box-shadow: 13px 7px 43px 3px rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6), 0 2px 4px 0 rgba(0, 0, 0, 0.12);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6), 0 2px 4px 0 rgba(0, 0, 0, 0.12);
}

@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(2);
            transform: scale(2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes App-logo-spin {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(2);
            transform: scale(2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



